.product {
    display: grid;
    grid-template-rows: 70% 30%;

    &__img {
        position: relative;
        cursor: pointer;
        overflow: hidden;
        border-radius: 10px;
        
        button {
            position: absolute;
            right: 10px;
            top: 10px;

            width: 35px;
            height: 35px;
            
            background-color: rgba(255, 255, 255, 0.564);
            border-radius: 50%;
            z-index: 5;

            &:hover { opacity: .8;}
        }

        &-photo {
            width: 100%;
            height: 100%;
            -o-object-fit: cover;
               object-fit: cover;
            
               -webkit-transition: -webkit-transform .5s;
            transition: -webkit-transform .5s;
            -o-transition: transform .5s;
            transition: transform .5s, -webkit-transform .5s;

               &:hover{
                -webkit-transform: scale(1.2);
                    -ms-transform: scale(1.2);
                        transform: scale(1.2);
            }
        }
    }


    &__preview {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 50% 50%;
        background-color: rgba(245, 242, 242, 0.235);

        &-title {
            font-weight: 100;
            padding-top: 10px;
            padding-bottom: 10px;
            overflow: hidden;

            &::first-letter {
                text-transform: uppercase;
            }
        }

        &-buy {
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-pack: justify;
                -ms-flex-pack: justify;
                    justify-content: space-between;
            -webkit-box-align: center;
                -ms-flex-align: center;
                    align-items: center;
            padding: 0 10px;
            p {
                font-size: 26px;
                font-weight: 500;
            }

            a {
                width: 35px;
                height: 35px;

                background-color: #ff6163;
                border-radius: 50%;
                background-image: url('../../assets/navigation/outline_shopping_cart_white_24dp.png');
                background-repeat: no-repeat;
                background-size: 60%;
                background-position: center, center;

                &:hover {
                    opacity: .7;
                }
            }
        }
    }
}
@media screen and (max-width: 700px) {
    .product{
        &__preview{
            &-title{
                font-size: 14px;
            }
            &-buy{
                p{
                    font-size: 20px;
                }
            }
        }
    }
}

